import * as actionTypes from '../constants/action-types';
import API from './API';

export const setUser = user => ( {
    type: actionTypes.SET_USER,
    payload: user
} )


// Action Creators

export const fetchUser = () =>
    async dispatch => dispatch(
        setUser( await API.user.current() )
    );

