import { rowStyle, segmentStyle } from './config';


const Home = props => {

  const { newDocuments } = props;

  return (
    <Grid relaxed columns="equal" className="cinch account-home-dashboard">
      <Grid.Column>

        <Grid.Row style={rowStyle}>
          <ProgramSearch style={segmentStyle} />
        </Grid.Row>

        <Grid.Row style={ rowStyle }>
          <DocumentFeed style={ segmentStyle } documents={ newDocuments } />
        </Grid.Row>

        <Grid.Row style={ rowStyle }>
          <UpcomingPayments style={ segmentStyle } />
        </Grid.Row>
      </Grid.Column>

      <Grid.Column width={ 4 }>
        <QuickAddMenu />
      </Grid.Column>

  </Grid>
  );
};

export default Home;

