const updateOrganizations = organizations => ( {
    type: actionTypes.UPDATE_ORGANIZATIONS,
    payload: organizations,
})


// Action Creators

export const fetchOrganizations = () =>
    async dispatch => {
        let organizations = await API.organizations.all();
        console.log('fetch orgs dispatch == ', organizations)
        return dispatch(
            updateOrganizations( organizations ) )
    };



