const API_VERSION = "/api/v1";

export default {

  test: {
    currentUser: function () {
      return _fetch( '/users/current?include=person' )
    },
    postUser: function () {
      const newUser = {
        data:
        {
          email: 'test@test.com ',
          first_name: 'One',
          last_name: 'Test',
          password: 'password',
          password_confirmation: 'password'
        }
      }
      return _fetch( '/auth/register', 'POST', newUser)
      
    },


  },

  contexts: {
    current: function () {
      return _fetch( '/users/current?include=contexts' )
        .then( _extractContexts )
        .catch( error => {
          console.log( 'Error while retrieving contexts.', error )
          return error
        })
    },
  },

  user: {
    current: function () {
      return _fetch( '/users/current?include=person' )
    }
  },

  organizations: {
    all: function () {
      console.log('getting all organizations - fetchAPI - ')
      return _fetch( '/organizations' )
    }
  }
}






function _fetch( url='', method = "GET", payload = {} ) {
  return fetch( _urlPrefix( url ), {
    method,
    headers: _ensureHeaders(),
    body: JSON.parse(payload)

  })
    .then( _parseResponse )
    .catch( _logError )

};

function _urlPrefix( url ) {
  return API_VERSION + url;
};

function _parseResponse( response ) {
  return response.json()
}


function _extractContexts( response ) {
  let r = response.data.contexts.data;
  let contexts = {
    household: r.find( c => c.context_type === 'household' ),
    organization: r.find( c => c.context_type === 'organization' ),
    program: r.find( c => c.context_type === 'program' ),
  }
  return contexts;
}


function _logError( error ) {
  return console.log( 'Error while fetching from API: ', error );
}

function _ensureHeaders() {
  return  {
      'X-Requested-With': 'XMLHttpRequest',
      "Content-Type": "application/json; charset=utf-8"
      // 'X-CSRF-TOKEN': window.axios.defaults.headers.common[ 'X-CSRF-TOKEN'
  }
}
