import React from "react";
import { Table as STable } from "semantic-ui-react";
import { ErrorBoundary, Panel } from "..";

export default class Table extends React.Component {
  render() {
    const classname = this.props.className ? this.props.className : "";
    return (
      <Panel.Group>
        {this.props.heading && (
          <Panel.Header className="table-header">{this.props.heading}</Panel.Header>
        )}
        <Panel.Item>
          <STable
            className={`cinch table ${classname}`}
            sortable
            padded="very"
            singleLine
          >
            {this.props.headerRow && this.props.headerRow}
            <STable.Body>{this.props.children}</STable.Body>
          </STable>
        </Panel.Item>
      </Panel.Group>
    );
  }
  static Row(props) {
    return <STable.Row {...props}>{props.children}</STable.Row>;
  }

  static Cell(props) {
    return <STable.Cell {...props}>{props.children}</STable.Cell>;
  }

  static HeaderRow({ columnNames }) {
    return (
      <STable.Header fullWidth>
        <STable.Row>
          {columnNames.map((columnName, i) => {
            if (columnName === null) {
              return <STable.HeaderCell key={i} />;
            }
            return <STable.HeaderCell key={i}>{columnName}</STable.HeaderCell>;
          })}
        </STable.Row>
      </STable.Header>
    );
  }

  static ToolBar( { className, children } ) {
    const additionalClasses = className ? className : '';
    return (
      <ErrorBoundary>
        <div className={`toolbar ${additionalClasses}`}>
          { children }
        </div>
      </ErrorBoundary>
    )
  }

  static ToolBarMenu(props) {
    return (
      <span className="tool-bar-menu">
        { props.children }
      </span>
    )
  }
}
