import React from "react";
import { Form } from "semantic-ui-react";
import { Dropdown } from '../../common';
import { doesNotContainLetters } from "../../../utilities";

import { expiration_years, months } from '../../../constants/static-data';

class AddNewPaymentMethod extends React.Component {
  state = {
    cardHolder: "",
    cardNumber: "",
    exp_month: "",
    exp_year: "",
    cvv: "",
    shareWithHousehold: false
  };

  handleChange = (event, info) => {
    let { name, value } = info;
    if (name === "cvv") {
      if (!this.validateCvvInput(value)) {
        return;
      }
    }
    this.setState({ [info.name]: info.value });
  };

  validNumber = value => doesNotContainLetters(value);

  validateCvvInput = value => {
    if (doesNotContainLetters(value) && value.length < 4) {
      return true;
    }
    return false;
  };

  validCardNumberInput = value => {};

  render() {
    const {
      cardHolder,
      cardNumber,
      exp_month,
      exp_year,
      cvv,
      shareWithHousehold
    } = this.state;

    return (
      <Form className="add-new-payment-method-form">
        <Form.Group widths="equal">
          <Form.Input
            tabIndex={0}
            value={cardHolder}
            name="cardHolder"
            required
            onChange={this.handleChange}
            label="Cardholder"
            placeholder="Cardholder Name"
            width={7}
          />
          <Form.Input
            tabIndex={0}
            value={cardNumber}
            name="cardNumber"
            required
            onChange={this.handleChange}
            label="Card Number"
            placeholder="Card Number"
            width={7}
          />
        </Form.Group>

        <Form.Group widths="equal" inline>
          <Dropdown
            fluid
            tabIndex={0}
            value={exp_month}
            name="exp_month"
            required
            onChange={this.handleChange}
            placeholder="Month"
            options={ months }
            label="month"
          />
          <Dropdown
            fluid
            tabIndex={ 0 }
            value={exp_year}
            name="exp_year"
            required
            onChange={this.handleChange}
            placeholder="Year"
            options={expiration_years}
          />
          <Form.Input
            tabIndex={0}
            name="cvv"
            value={cvv}
            required
            onChange={this.handleChange}
            label="cvv"
            placeholder="CVV"
            className="new-payment-method-cvv"
          />
        </Form.Group>
        <Form.Group widths="equal">
          <Form.Checkbox label="Share with Household?" />
          <Form.Button>Save</Form.Button>
        </Form.Group>
      </Form>
    );
  }
}

export default AddNewPaymentMethod;
