const initialState = {
  test: "Initial State Test",
}

const initialContextsState = {
  household: null,
  organization: null,
  program: null,
}

const initialUserState = {
  person: null,
  households: null,
  organizations: null,
  programs: null,
};

const initialHouseholdsState = {
  current: null,
  all: [],
};

const initialOrganizationsState = {
  current: null,
  all: [],
};

const initialProgramsState = {
  current: null,
  all: [],
};

const contextsReducer = ( state = initialContextsState, action ) => {
  switch ( action.type ) {
    case actionTypes.LOAD_CONTEXTS:
      console.log( 'context reducer :: load contexts :: ', action)
      return {
        ...state,
        ...action.payload
      }
    default:
      return state;
  }
}


const userReducer = ( state = initialUserState, action ) => {
  switch ( action.type ) {

    case actionTypes.SET_USER:
      return {
        ...state,
        ...action.payload.data,
      }

    case actionTypes.UPDATE_USER:
      return { ...state, currentUser: action.payload };

    case actionTypes.UPDATE_USER_ORGANIZATION:
      return { ...state, userOrg: action.payload };

    case actionTypes.SET_USER_HOUSEHOLD:
      return { ...state, household: action.payload }

    default:
      return state;
  }
}

const householdsReducer = ( state = initialHouseholdsState, action ) => {
  switch ( action.type ) {

    case actionTypes.SET_HOUSEHOLD:
      return { ...state, current: action.payload }

    case actionTypes.UPDATE_HOUSEHOLD_MEMBERS:
      return { ...state, householdMembers: action.payload }

    case actionTypes.UPDATE_HOUSEHOLD:
      return { ...state, household: action.payload }

    default:
      return state;
  }
}


const organizationsReducer = ( state = initialOrganizationsState, action ) => {
  switch ( action.type ) {
    case actionTypes.UPDATE_ORGANIZATIONS:
      return { ...state, all: action.payload };

    case actionTypes.UPDATE_ORGANIZATION:
      return { ...state, organization: action.payload };
    case actionTypes.UPDATE_ORGANIZATION_TYPES:
      return { ...state, organizationTypes: action.payload };
    case actionTypes.UPDATE_ORG_PROGRAMS:
      return { ...state, orgPrograms: action.payload };

    default:
      return state;
  }
}

const programsReducer = ( state = initialProgramsState, action ) => {
  switch ( action.type ) {
    case actionTypes.UPDATE_PROGRAM_TYPES:
      return { ...state, programTypes: action.payload }
    case actionTypes.UPDATE_PROGRAM_REGISTRATIONS:
      return { ...state, programRegistrations: action.payload }
    default:
      return state;
  }
}


