export default combineReducers( {
  context: contextsReducer,
  user: userReducer,
  household: householdsReducer,
  organization: organizationsReducer,
  program: programsReducer,
  // root: rootReducer1,
  // form: formReducer,
  // modal: modalReducer,
} );