import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
} from "react-router-dom";
import { Grid } from "semantic-ui-react";

import { connect } from "react-redux";
import { fetchContexts } from './actions/contexts';
import { fetchUser } from './actions/users';
import { fetchOrganizations } from './actions/organizations';


import { ErrorBoundary, NavBar } from "./Components/common";
import { Admin, Account, Hq } from "./Pages";


class App extends React.Component {
  componentDidMount() {
    // let token = document.head.querySelector( 'meta[name="csrf-token"]' );
    // this.initializeGlobalState();
    // console.log( 'CSRF TOKEN :: ', token );
  }

  initializeGlobalState = () => {
    // const { fetchContexts, fetchOrganizations, fetchUser } = this.props;
    // const promises = [
      // fetchContexts(),
      // fetchUser(),
      // fetchOrganizations()
  //   ];
  //   Promise.all( promises );
  }

  Account = props =>
    <Account { ...props } user={ this.props.user } />

  Hq = props =>
    <Hq { ...props } user={ this.props.user } />

  Admin = props =>
    <Admin { ...props } user={ this.props.user } />

  render() {

    let isOrgAdmin = true;

    return (
        <ErrorBoundary>
      <Router>
          <Grid
            className="cinch"
            stretched
            stackable={false}
        >
          {isOrgAdmin && (
            <Grid.Column stretched className="cinch-side-column">
              <span>Admin Bar</span>
            </Grid.Column>
          )}

            <Grid.Column className="cinch-main-column">
              <Grid.Row  className="cinch-navbar-row">
                <NavBar />
              </Grid.Row>

              <Grid.Row className="cinch-main-row">
                <Switch>
                  <Route path={`/app/account`} component={this.Account} />
                  <Route path={`/app/hq`} component={this.Hq} />
                  <Route path={`/app/admin`} component={this.Admin} />
                </Switch>
              </Grid.Row>
            </Grid.Column>
          </Grid>
        </Router>
            </ErrorBoundary>
    );
  }
}

const mapStateToProps = state => ({
  contexts: state.contexts,
  user: state.user,
} );

const mapDispatchToProps = {
  fetchContexts,
  fetchOrganizations,
  fetchUser,
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
