import React from "react";
import { Form, Grid, Image, Divider, Select, Input } from "semantic-ui-react";
import { Header, Panel } from "../../../Components/common";

import { doesNotContainLetters } from "../../../utilities";
import {
  genders,
  us_states,
} from "../../../constants/static-data";

import avatarImage from "../../../images/icons/users/placeholder-avatar.png";

class NewHouseholdMemberForm extends React.Component {
  state = {
    newMember: {
      first_name: "",
      last_name: "",
      gender: "",
      birthdate: "",
      email: "",
      phone_number: "",
      address: {
        same_as_household: false,
        street: "",
        city: "",
        state: "",
        zip: ""
      },
      relationships: [],
      receive_communications: false,
      access_financials: false
    }
  };

  isDateData = name => (
    name === "day" ||
    name === "month" ||
    name === "year"
  );

  isLocationData = name => (
    name === "same_as_household" ||
    name === "city" ||
    name === "street" ||
    name === "state" ||
    name === "zip"
  );

  handleChange = (event, data) => {
    console.log('newhouseholdmember handle change :: ', data);
    let state = { ...this.state };

    if (this.isLocationData(data.name)) {
      if (data.name === "same_as_household") {
        state.newMember.address[data.name] = data.checked;
      } else {
        state.newMember.address[data.name] = data.value;
      }
    }

    if (this.isDateData(data.name)) {
      state.newMember.birthdate[data.name] = data.value;
    }

    if (data.name === "zip") {
      if (doesNotContainLetters(data.value)) {
        if (data.value.length < 6) {
          state.newMember[data.name] = data.value;
        }
      }
      return;
    }

    console.log('newhouseholdmember handle change 2  :: ', data );

    state.newMember[data.name] = data.value;
    this.setState(state);
  };

  render() {
    const {
      first_name,
      last_name,
      gender,
      birthdate,
      email,
      phone_number,
      address: { same_as_household, street, city, state, zip },
      relationships,
      receive_communications,
      access_financials
    } = this.state.newMember;

    return (
      <Panel.Group>
        <Panel.Header text="Add New Household Member" />
        <Panel.Item>
          <Form className="add-new-member-form">
            <Grid className="add-new-member-grid">
              <Grid.Row columns="equal" className="form-section">
                <Grid.Column stretched width={3}>
                  <div>
                    <Image src={avatarImage} size="tiny" />
                  </div>
                </Grid.Column>

                <Grid.Column>
                  <Form.Group widths={2}>
                    <Form.Input
                      width={8}
                      onChange={this.handleChange}
                      value={first_name}
                      label="First Name"
                      name="first_name"
                    />
                    <Form.Input
                      width={8}
                      value={last_name}
                      onChange={this.handleChange}
                      label="Last Name"
                      name="last_name"
                    />
                  </Form.Group>

                  <Form.Group widths={2}>
                    <Form.Field
                      control={Select}
                      options={genders}
                      onChange={this.handleChange}
                      value={gender}
                      name="gender"
                      label="Gender"
                    />
                    <Form.Field
                      control={Input}
                      placeholder={birthdate}
                      value={birthdate}
                      onChange={this.handleChange}
                      type="date"
                      name="birthdate"
                      label="Birthdate"
                    />
                  </Form.Group>
                </Grid.Column>
              </Grid.Row>

              <Divider />

              <Grid.Row columns="equal" className="form-section">
                <Grid.Column stretched width={3}>
                  <Header as="h5">Address</Header>
                  <Form.Checkbox
                    name="same_as_household"
                    onChange={this.handleChange}
                    checked={same_as_household}
                    label="Same as Household"
                  />
                </Grid.Column>

                <Grid.Column>
                  <Form.Input
                    disabled={same_as_household}
                    onChange={this.handleChange}
                    value={street}
                    name="street"
                    label="Street"
                  />
                  <Form.Group>
                    <Form.Input
                      disabled={same_as_household}
                      onChange={this.handleChange}
                      value={city}
                      name="city"
                      label="City"
                    />
                    <Form.Field
                      disabled={same_as_household}
                      control={Select}
                      options={us_states}
                      onChange={this.handleChange}
                      value={state}
                      name="state"
                      label="State"
                    />
                    <Form.Input
                      disabled={same_as_household}
                      onChange={this.handleChange}
                      value={zip}
                      name="zip"
                      label="Zip Code "
                    />
                  </Form.Group>
                </Grid.Column>
              </Grid.Row>

              <Divider />

              <Grid.Row columns="equal" className="form-section">
                <Grid.Column stretched width={3}>
                  <Header as="h5">Login</Header>
                </Grid.Column>

                <Grid.Column>
                  <Form.Group widths={2}>
                    <Form.Input
                      width={8}
                      value={email}
                      onChange={this.handleChange}
                      type="email"
                      name="email"
                      label="Email Address"
                    />
                    <Form.Input
                      width={8}
                      value={phone_number}
                      onChange={this.handleChange}
                      type="number"
                      name="phone_number"
                      label="Mobile Number"
                    />
                  </Form.Group>

                  {/*FIXME: The positioning of the checkbox is annoying*/}
                  <Form.Checkbox label="Send Invite" />
                </Grid.Column>
              </Grid.Row>

              <Divider />

              <Grid.Row columns="equal" className="form-section">
                <Grid.Column stretched width={3}>
                  <Header as="h5">Relationships</Header>
                </Grid.Column>

                <Grid.Column width={8}>
                  <Form.Group inline widths={2}>
                    <Form.Field
                      control={Select}
                      options={[]}
                      label="Household Member"
                    />
                    <Form.Field
                      control={Select}
                      options={[]}
                      label="Relationship"
                    />
                  </Form.Group>
                </Grid.Column>
                <Grid.Column width={5}>
                  <Form.Checkbox label="Receive Team Communications" />
                  <Form.Checkbox label="Access Financials" />
                </Grid.Column>
              </Grid.Row>

              <Divider />

              <Grid.Row columns="equal" className="f-row fcentered">
                <Form.Button>Save</Form.Button>
              </Grid.Row>
            </Grid>
          </Form>
        </Panel.Item>
      </Panel.Group>
    );
  }
}

export default NewHouseholdMemberForm;
