import React from "react";
import { connect } from "react-redux";
import { Switch, Route, withRouter } from "react-router-dom";
import { Grid } from "semantic-ui-react";

import {
  Home,
  Household,
  Financials,
  Documents,
  NewHouseholdMemberForm,
  Programs
} from "./index";

import { ErrorBoundary, NavMenu } from "../../Components/common";
import { MyPlayersPanel } from "../../Components/account/home";

// pull config data from file for now
import { fake_new_documents, fake_household_documents, links, householdJSON } from "./config";


const HOME = "home";

// const filterDocuments = (householdDocs, orgDocs) =>
//   orgDocs.filter(doc => !householdDocs.includes(doc));

class AccountPage extends React.Component {
  state = {
    currentPage: HOME
  };

  handleClick = (e, { name }) => {
    this.setState({ currentPage: name });
  };

  componentDidMount() {
    // this.checkUrl();
    this.setPageBasedOnUrl();
  }

  checkUrl = () => {
    if (this.props.location.pathname === "/app/account") {
      this.props.history.push("/app/account/home");
    } else {
    }
  };

  setPageBasedOnUrl = () => {
    let split = this.props.location.pathname.split("/");
    let path = split[split.length - 1];

    this.setState({ currentPage: path });
  };

  Home = props => <Home {...props} newDocuments={fake_new_documents} />;

  Household = props => (
    // <Household {...props} members={this.props.activeHousehold.members.data} />
    <Household {...props} members={householdJSON.data.members.data} />
  );

  Programs = props => (
    <Programs {...props} searchResults={this.props.programs} />
  );

  Documents = props => (
    <Documents
      {...props}
      viewDocumentFn={this.handleClick}
      documents={fake_household_documents}
    />
  );

  render() {
    const { currentPage } = this.state;
    const {
      // activeHousehold,
      // activeOrganization,
      // programs,
      match: { path }
    } = this.props;

    const newDocuments = null;
    // const newDocuments = filterDocuments(
    //   activeHousehold.documents,
    //   activeOrganization.documents
    // );

    const showMyPlayers = currentPage === HOME;

    return (
      <Grid columns="equal" className="account-page">
        <ErrorBoundary>

          <Grid.Column width={ 3 } className="nav-col">
            <NavMenu
              path={path}
              active={currentPage}
              clickFn={this.handleClick}
              links={links}
            />

            <MyPlayersPanel show={showMyPlayers} />
          </Grid.Column>

          <Grid.Column className="content-col">
            <Switch>
              <Route exact path={`${path}`} component={this.Home} />
              <Route
                exact
                path={`${path}/household`}
                component={this.Household}
              />
              <Route path={`${path}/programs`} component={this.Programs} />
              <Route
                path={`${path}/household/add_new_member`}
                component={NewHouseholdMemberForm}
              />
              householdJSON
              <Route path={`${path}/financials`} component={Financials} />
              <Route path={`${path}/documents`} component={this.Documents} />
            </Switch>
          </Grid.Column>

        </ErrorBoundary>
      </Grid>
    );
  }
}

const mapStateToProps = state => ({});

const ConnectedAccountPage = withRouter(connect(mapStateToProps)(AccountPage));
// const ConnectedAccountPage = withRouter(connect(mapStateToProps)(AccountPage));

export default ConnectedAccountPage;
