import React from 'react';
import { Link } from 'react-router-dom';
import { Image } from 'semantic-ui-react';
import { Table } from '../../../../Components/common';

import caretRight from '../../../../images/icons/caret-right.png'

const SearchResultRow = ( {
  id, logo, org_name, program_name, location, dates, price, path
}) => (
  <Table.Row>
    <Table.Cell>{logo}</Table.Cell>
    <Table.Cell>{org_name}</Table.Cell>
    <Table.Cell>{program_name}</Table.Cell>
    <Table.Cell>{location}</Table.Cell>
    <Table.Cell>{dates}</Table.Cell>
    <Table.Cell>{price}</Table.Cell>
    <Table.Cell>
      <Link to={`${path}/${id}/view`}>
        <Image src={caretRight} />
      </Link>
    </Table.Cell>
  </Table.Row>
)

export default SearchResultRow;