import React from "react";
import { Link } from "react-router-dom";
import { Menu, Image } from "semantic-ui-react";

import { capFirstLetter } from "./config";

const NavMenu = ({active, className, clickFn, links, path}) => {
  const classnames = className ? className : '';
  return (
    <Menu pointing vertical className={`nav-menu ${classnames}`}>
      { links.map( ( link, i ) =>
        <Menu.Item
          key={link.name + i}
          name={link.name}
          active={active === link.name}
          onClick={clickFn}
          link
          to={ link.name === 'home' ? `${path}` : `${path}/${link.name}` }
          as={ Link }
          className="nav-item"
        >
        <Image avatar centered verticalAlign="middle" src={link.icon} className="icon" />
        <span>
          { capFirstLetter(link.name) }
        </span>
      </Menu.Item>
      ) }
    </Menu>
  );
};

export default NavMenu;