export const getNumberRange = (min, max, storage) => {
  if (storage.length > (max - min)) {
    return storage.map(num => num.toString())
  }
  let newNumber = min + storage.length
  storage.push(newNumber)
  return getNumberRange(min, max, storage)
}

export const doesNotContainLetters = value => {
  if ( value.search( /([A-Za-z])/ ) === -1 ) {
    return true;
  }
  return false;
}



export const toggleGrayBackground = on => {
  if ( on ) {
    document.body.classList.add( 'gray-body' );
  } else {
    document.body.classList.remove( 'gray-body' );
  }
}