import React from 'react'
import { Header, Table } from '../../../Components/common';
import DocumentRow from './DocumentRow';
import { columnNames } from './config';


const _documents = arrayOfDocuments => arrayOfDocuments.map( ( document, i ) =>
  <DocumentRow key={ i } {...document} />
)

const toolBar = (
  <Table.ToolBar>
    <Table.ToolBarMenu>
      <Header>Documents</Header>
    </Table.ToolBarMenu>
  </Table.ToolBar>
);

const headerRow = (
  <Table.HeaderRow columnNames={columnNames} />
)


const Documents = ({documents}) => {
  const renderDocuments = documents ? _documents(documents) : null

  return (
    <Table heading={ toolBar } headerRow={ headerRow }>
      { renderDocuments }
    </Table>
  )
}

export default Documents;