import React from "react";
import { Link } from 'react-router-dom';
import { Button, Header, Table } from '../../../Components/common';

import MemberRow from './MemberRow';
import { columnNames, ADD_NEW_MEMBER_URL } from './config';


class Household extends React.Component {
  state = {
    editIndex: null,
  };

  enableEdit = ( event, data ) => {
    console.log('account app -> household -> enable edit member')
    this.setState( { editIndex: data.index } );
  }

  saveEdit = ( updatedValues ) => {
    console.log( 'account app -> household -> update member :: ', updatedValues )
    this.setState({ editIndex: null })
    //TODO: get user ID to target update request
    // take values and submit to api
    // after success -> dispatch getMembers
  }

  deleteMember = ( event, data ) => {
    console.log('account app -> household -> delete member')
    //TODO: receive user ID and send with delete request
  }

  headerRow = () => <Table.HeaderRow columnNames={ columnNames } />;

  toolBar = () => (
    <Table.ToolBar>
      <Table.ToolBarMenu>
        <Header>Members</Header>
      </Table.ToolBarMenu>
      <Button as={ Link } to={ADD_NEW_MEMBER_URL} color="black">
        Add New
      </Button>
    </Table.ToolBar>
  );

  _members = arrayOfMembers => {
    const { editIndex } = this.state;

    return (
      arrayOfMembers.map( ( member, i ) => {
        const editEnabled = i === editIndex;
        console.log( editEnabled )
        return (
          <MemberRow
            edit={ editEnabled }
            key={ i }
            index={ i }
            editFn={ this.enableEdit }
            delFn={ this.deleteMember }
            saveFn={ this.saveEdit }
            { ...member }
          />
        )
      }
      )
    )
  };

  render() {

    const memberRows = this.props.members
      ? this._members( this.props.members )
      : null;


    return (
      <Table heading={ this.toolBar() } headerRow={ this.headerRow() }>
        {memberRows}
      </Table>
    )
  }
};

export default Household;