import React from "react";
import { Image, Input, Select } from "semantic-ui-react";
import { Button, Table } from "../../../Components/common";
import EditMenu from "../../../Components/account/household/EditMenu";

import { genders } from '../../../constants/static-data';

import placeholderAvatar from "../../../images/icons/profile.png";

class MemberRow extends React.Component {

  state = {
    first_name: null,
    last_name: null,
    birthdate: null,
    gender: null,
  };

  componentWillReceiveProps(nextProps) {
    if ( nextProps.edit && !this.props.edit ) {
      this.loadDataIntoState( nextProps );
    } else if ( !nextProps.edit && this.props.edit ) {
      this.clearState();
    }
  }

  loadDataIntoState = ( {
    first_name,
    last_name,
    birthdate,
    gender,
  } ) => {
    console.log('editing')
    this.setState( {
      first_name, last_name, birthdate, gender
    } )
  }

  clearState = () => {
    this.setState( {
      first_name: null,
      last_name: null,
      birthdate: null,
      gender: null,
    } )
  }

  handleChange = ( event, data ) => {
    this.setState({[data.name]: data.value})
  }

  saveChanges = () => {
    this.props.saveEdit(this.state)
  }

  displayRow = ( {
    first_name,
    last_name,
    birthdate,
    gender,
    editFn,
    delFn,
    index,
  } ) => {
    return (
      <Table.Row>
        <Table.Cell>
          <Image avatar src={ placeholderAvatar } />
        </Table.Cell>
        <Table.Cell>{ first_name }</Table.Cell>
        <Table.Cell>{ last_name }</Table.Cell>
        <Table.Cell>{ birthdate }</Table.Cell>
        <Table.Cell>{ gender }</Table.Cell>
        <Table.Cell textAlign="center">
          <EditMenu editFn={ editFn } delFn={ delFn } index={ index }/>
        </Table.Cell>
      </Table.Row>
    )
  };

  editRow = () => {
    const { first_name, last_name, birthdate, gender } = this.state;
    return (
      <Table.Row>

      <Table.Cell>
        <Image avatar src={placeholderAvatar} />
      </Table.Cell>
          <Table.Cell>
          <Input
            size='mini'
            value={ first_name }
            name="first_name"
            onChange={ this.handleChange }
          />
        </Table.Cell>
        <Table.Cell>
          <Input
            size='mini'
            value={ last_name }
            name="last_name"
            onChange={ this.handleChange }
            />
      </Table.Cell>
      <Table.Cell>
          <Input
            size='mini'
            width={2}
            value={ birthdate }
            onChange={ this.handleChange }
            name="birthdate"
          />
      </Table.Cell>
      <Table.Cell>
            <Select
              placeholder={ gender }
              value={ gender }
              options={genders}
              onChange={ this.handleChange }
              name="gender"
            />
      </Table.Cell>
      <Table.Cell textAlign="center">
        <Button onClick={ this.props.saveFn }>Save</Button>
      </Table.Cell>
    </Table.Row>
    )
  }

  render() {
    if ( this.props.edit ) {
      return this.editRow( this.props );
    }
    return this.displayRow( this.props );

  }
}

export default MemberRow;

