import React from "react";
import { Link } from "react-router-dom";
import { Form, Select } from "semantic-ui-react";
import { Button, Panel } from "../../common";

import { sports, us_states } from '../../../constants/static-data';
import { doesNotContainLetters } from "../../../utilities";

class ProgramSearch extends React.Component {
  state = {
    program_name: "",
    sport: "",
    state: "",
    zip_code: ""
  };

  handleChange = (event, data) => {
    // Limit the zip code to numerical values only and a max length of 5 characters.
    if (data.name === "zip_code") {
      if (doesNotContainLetters(data.value)) {
        if (data.value.length < 6) {
          this.setState({ [data.name]: data.value });
        }
      }
      return;
    }
    this.setState({ [data.name]: data.value });
  };

  handleSubmit = () => {
    console.log("Send search query with following parameters :: ");
    console.log(this.state);
  };

  render() {
    const { program_name, sport, state, zip_code } = this.state;
    const { searchByName } = this.props;
    return (
      <Panel.Group>
        <Panel.Header text="Find A Program" />

        <Panel.Item className="f-row">
          <Form className="program-search-form">
            <Form.Group widths={ 3 } >

              { searchByName && (
                <Form.Input
                  name="program_name"
                  onChange={ this.handleChange }
                  placeholder="Search"
                  value={ program_name }
                  tabIndex={ 0 }
                  label="Organization"
                  className="program-search-input"
                />
              )}

              <Form.Field
                width={8}
                control={Select}
                options={sports}
                onChange={this.handleChange}
                value={ sport }
                name="sport"
                placeholder="Sport"
                label="Sport"
                className="program-search-select"
              />
              <Form.Field
                width={7}
                control={Select}
                onChange={this.handleChange}
                options={us_states}
                value={ state }
                name="state"
                label="State"
                className="programs-search-select"
              />
              <Form.Input
                name="zip_code"
                onChange={this.handleChange}
                value={zip_code}
                tabIndex={0}
                width={ 6 }
                label="Zip"
                className="program-search-input"
              />
              <Button
                as={Link}
                to="/app/account/programs"
                onClick={this.handleSubmit}
                color="black"
              >
                Search
              </Button>
            </Form.Group>
          </Form>
        </Panel.Item>
      </Panel.Group>
    );
  }
}

export default ProgramSearch;
