import React from 'react';
import { Radio, Image, Grid } from 'semantic-ui-react';
import { Button, Panel } from '../../common';

import visaLogo from '../../../images/creditcard_logos/visa.png';
import mastercardLogo from '../../../images/creditcard_logos/mastercard.png';

const cardLogos = {
  mastercard: mastercardLogo,
  visa: visaLogo,
};

const cards = [
  {type: 'visa', isDefault: true, lastFourDigits: '4656'},
  {type: 'mastercard', isDefault: false, lastFourDigits: '7351'},
  {type: 'visa', isDefault: false, lastFourDigits: '8346'},
]



const SavedPaymentMethod = ({type, isDefault, lastFourDigits}) => {

  return (
    <Grid.Row className="saved-payment-method-item">
      <span>
        <Radio name="selected_method" />
        <Image src={ cardLogos[type] } />
      </span>
      * { lastFourDigits } {isDefault && ("  ( Default )")}
    </Grid.Row>
  )
};

const SavedPaymentMethods = props => {

  const savedMethods = cards.map( ( card, i ) => <SavedPaymentMethod key={i} { ...card } /> );
  return (
    <React.Fragment>
      { savedMethods }
      <Grid.Row>
        <Button>Save as Default</Button>
      </Grid.Row>
    </React.Fragment>
  )
};

export default SavedPaymentMethods;