import React from 'react';
import { Table } from '../../../../Components/common';
import ProgramRow from './ProgramRow';

export const columnNames = [
  "Organization",
  "Name",
  "Program",
  "Location",
  "Dates",
  "Price",
]

const _programRows = ( arrayOfResults, path ) =>
  arrayOfResults.map( (result, i) => <ProgramRow key={ i } path={path} { ...result } /> )


const _headerRow = () => <Table.HeaderRow columnNames={ columnNames } />;

const ProgramsTable = ({ programs, match: { path } }) => {
  console.log(path)
  const programRows = programs
    ? _programRows( programs, path )
    : null

  return (
    <Table headerRow={ _headerRow() }>
      { programRows }
    </Table>
  )
}

export default ProgramsTable;
